.sl {
  &-cus {
    position: relative;
    &.min {
      min-width: 12rem;
      &:not(:first-child) {
        margin-left: 0.5rem;
      }
    }
  }
  &-head {
    background-color: #f8f8f8;
    height: 4rem;
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 0 1.5rem;
    @include flex;
    align-items: center;
    position: relative;
    z-index: 5;
    cursor: pointer;
    border-radius: 0.6rem;
    &::before {
      content: "";
      position: absolute;
      right: 10px;
      top: 13px;
      width: 0;
      height: 0;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      border-bottom: 5px solid var(--color-sec);
    }
    &::after {
      content: "";
      position: absolute;
      right: 1rem;
      bottom: 1.3rem;
      width: 0;
      height: 0;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      border-top: 5px solid var(--color-sec);
    }
  }
  &-main {
    font-size: 1.5rem;
    line-height: 1.5;
    font-weight: 700;
    letter-spacing: 0.5px;
    @include lc(1);
  }
  &-list {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-top: none;
    background-color: white;
    z-index: 10;
    display: none;
    overflow: hidden;
    border-radius: 0 0 6px 6px;
    max-height: 20rem;
    overflow-y: auto;
  }
  &-item {
    font-size: 1.4rem;
    line-height: 1.5;
    letter-spacing: 0.5px;
    color: var(--color-black);
    padding: 1rem 1.5rem;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    &:hover {
      background-color: var(--color-sec);
      color: white;
    }
  }
}
